import React, {Component} from 'react';
var tableValue = document.querySelectorAll(".js-value");
var setButton = document.querySelectorAll(".js-set-button");
var chart = document.querySelector(".js-chart");

var data = {

};

var options = {

};

class Set extends Component {

	setGraph = () => (e) => {
		for (var i = 0; i < setButton.length; i++) {
			setButton[i].classList.remove("active");
		}
		e.currentTarget.classList.add("active");

		data.series[0].data = [];
		chart.update(data, options, true);

		var dataValue = e.currentTarget.getAttribute("data-set");

		Array.prototype.forEach.call(tableValue, function (item) {
			var dataTable = item.getAttribute("data-set");
			if (dataTable === dataValue) {
				data.series[0].data.push(item.innerHTML);
			}
		});
		chart.update(data, options, true);
	};

	render() {

		return (
			<div className="set">
				<div className="set__wrapper">
					<button type="button" className="set__button js-set-button" onClick={this.setGraph}
					        data-set="Total-Unique-User">Total Unique User
					</button>
					<button type="button" className="set__button js-set-button" onClick={this.setGraph}
					        data-set="Total-Leads">Total Leads
					</button>
					<button type="button" className="set__button js-set-button" onClick={this.setGraph}
					        data-set="Total-Trx">Total Trx
					</button>
					<button type="button" className="set__button js-set-button" onClick={this.setGraph}
					        data-set="Total-Volume">Total Volume
					</button>
					<button type="button" className="set__button js-set-button" onClick={this.setGraph}
					        data-set="Total-Income">Total Income
					</button>
					<button type="button" className="set__button js-set-button" onClick={this.setGraph}
					        data-set="Total-Webmasters">Total Webmasters
					</button>
					<button type="button" className="set__button js-set-button" onClick={this.setGraph}
					        data-set="Total-Webmaster-Income">Total Webmaster Income
					</button>
					<button type="button" className="set__button js-set-button active" onClick={this.setGraph}
					        data-set="Total-Net-Income">Total
						Net Income
					</button>
				</div>
			</div>

		);
	}
}

export default Set;


