import React, { Component } from 'react';
class Header extends Component {
	render() {
		return (
				<header className="header">
					<div className="container">
						<div className="header__blocks">
							<div className="header__block">
								<div className="logo">
									<a className="logo__link" href="index.html" title="   ">
										<img className="logo__pic" src="images/logo.svg" alt="   "></img>
									</a>
								</div>
							</div>
							<div className="header__block header__block_wide">
								<div className="menu">
									<nav>
										<ul className="menu__list">
											<li className="menu__item">
												<a className="menu__link" href="index.html">Statistics</a>
											</li>
											<li className="menu__item">
												<a className="menu__link" href="list.html">List of Webmasters</a>
											</li>
											<li className="menu__item">
												<a className="menu__link" href="load.html">Load Promo</a>
											</li>
										</ul>
									</nav>
								</div>
							</div>
							<div className="header__block">
								<a href="#" className="logout">
									<span className="icon icon_logout"></span>
									<span className="logout__text">Exit</span>
								</a>
							</div>
						</div>
					</div>
				</header>

		);
	}
}

export default Header;


