import $ from 'jquery';
import React from 'react';
import ReactDOM from 'react-dom';
import './styles/reboot.css';
import "chartist/dist/chartist.min.css";
import "chartist/dist/chartist.min.js";
import './styles/style.css';
import registerServiceWorker from './registerServiceWorker';
import Header from './blocks/header';
import Chartist from './blocks/chartist';
import Set from "./blocks/set";
import Download from "./blocks/download";
import Table from "./blocks/table";
import Paginator from "./blocks/paginator";
function addVoidForLinks(links) {
	$.each(links, function () {
		if ($(this).attr("href") === "" || $(this).attr("href") === "#") {
			$(this).attr("href", "javascript:void(0)");
		}
	});
}

ReactDOM.render(
	<div className="wrapper">
		<div className="content">
			<Header></Header>
			<main>
				<div className="container">
					<Chartist/>
					<Set/>
					<Download/>
					<Table/>
					<Paginator/>
				</div>
			</main>
		</div>
	</div>
, document.getElementById('root'));

registerServiceWorker();

addVoidForLinks($("a"));



