import React, { Component } from 'react';
import ChartistGraph from 'react-chartist';
var data = {

};

var options = {

};

var type = 'Line';

class Chartist extends Component {

	render() {

		return (
			<div>
				<ChartistGraph className={'js-chart'} data={data} options={options} type={type} />
			</div>
		)
	}
}
export default Chartist;


